import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { NavBar, Cell, Switch, Button, Dialog } from 'react-vant';
import useStore from '../store';
import { setPersistAuth, getPersistAuth, removePersistAuth } from '../utils';

export default function Settings() {
  const navigate = useNavigate();
  const [rememberToken, setRememberToken] = useState(getPersistAuth());
  const logout = useStore((state) => state.logout);

  return (
    <div>
      <NavBar safeAreaInsetTop title="" leftArrow={false} />
      <Cell.Group border={false} title="">
        <Cell border={false} center title="">
          <Switch
            size={24}
            checked={rememberToken}
            onChange={(changedValue) => {
              setRememberToken(changedValue);
              setPersistAuth(changedValue);
            }}
          />
        </Cell>
      </Cell.Group>

      <div className="settings-footer">
        <Button
          type="primary"
          block
          round
          onClick={() => {
            Dialog.confirm({
              title: '',
              message: '?',
            })
              .then(() => {
                logout();
                //  auth
                removePersistAuth();
                navigate('/login', { replace: true });
              })
              .catch(() => {
                // 
              });
          }}
        >
          
        </Button>
      </div>
    </div>
  );
}
